@mixin flex  {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

$primary: #f7d372;
$secondary : rgb(247,114,189);
$shadow: 3px 3px 8px 2px  #0000009c;

body {
  background: rgb(243,233,235);
  padding-bottom: 100px;
}

header {
  margin: 20px;
  position: relative;
  color: #f76c84;
  font-size: 20px;
  span{
    font-size: 30px;
    color: white;
    font-style: italic;
    font-weight: bold;
    &::after {
      position: absolute;
      content: "";
      height: 50px;
      width: 50px;
      background: $primary;
      border-radius: 200px 44px 30px 20px;
      left: 37px;
      z-index: -10;
      opacity: 0.8;
      top: -7px;
    }
    &::before {
        position: absolute;
      content: "";
      height: 50px;
      width: 50px;
      background: $secondary;
      border-radius: 200px 44px 30px 20px;
      left: 55px;
      z-index: -10;
      transform: rotate(20deg);
      top: -5px;
      opacity: 0.8;
      } 
  } 
}

.sub {
  @include flex;
  p {margin-top: -23px;} 
}

.add {
    @include flex;
    color: $secondary;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin: 30px auto 10px;
    border: 2px $secondary solid;
    transition: all 0.3s ease-in-out;
    &:hover {
      background: $secondary;
      color: white;
      border: 2px $secondary solid;
    }
  label {
    font-size: 20px;
    padding: 10px;
  };
  input {
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
  }
} 

.file, .error {
  @include flex;
} 

.progress {
  max-width: 70%;
  margin: 20px auto 20px;
  height: 5px;
  background: $primary;
  border-radius: 5px;
  
}

.success {
    @include flex;
  p {
  padding: 4px 15px;  
  color: green;
  font-size: 10px;
  border-radius: 10px;
  box-shadow: 3px 3px 8px 2px  rgb(173,173,173);
  }
}

.image-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-gap: 40px;
  padding: 0 20px;
  margin-top: 200px;
 .image-wrap {
   position: relative;
   height: 0;
   overflow: hidden;
   padding: 50% 0;
   img {
     min-width: 100%;
     min-height: 100%;
     max-height: 150%;
     position: absolute;
     top: 0;
     left: 0;
     
   }
 }
}

.modal {
  @include flex;
  position: fixed;
  top: 0;
  left: 0;
  background: rgb(0,0,0, 0.5);
  height: 100%;
  width: 100%;
  img {
    
    max-height: 80%;
    max-width: 60%;
    border: 4px white solid;
    box-shadow: $shadow;
    
  }
}

footer {
  @include flex;
  margin-top: 100px;
  p {
    font-size: 15px;
    font-weight: 500;
   .logo {
     color: red;
   }
  .icon-1, .icon-2 {
    font-size: 15px;
    color: #b80202;
  }
  
 .icon-2 {
   color: black;
 }
  }
}

#about {
  @include flex;
  color: black;
}

.about {
  padding: 20px;
}

.socials {
  margin-bottom: 50px;
 .social {
  .icon {
    font-size: 30px;
    margin-right: 15px;
    transition: all 0.3s ease-in-out;
    text-decoration: none;
    &:hover {
      opacity: 0.7;
      transform: translateY(-10px);
    }
  }
 }
}

@media (min-width: 688px){
 .image-grid {
  margin-top: 100px;
  padding: 0 20px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-gap: 40px;
 .image-wrap {
    height: 0;
    overflow: hidden;
    padding: 50% 0px;
    position: relative;
    img {
      position: absolute;
      min-height: 100%;
      min-width: 100%;
      max-height: 150%;
      top: 0;
      left: 0;
    }
  }
  
}



}